.App {
	position: relative;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	background-color: #DDD;
}


/*Common class for all containers with in and out animations*/
.MainWrapper {
	position: relative;
	width: 100%;
	height: 100%;
}

.MainWrapper.on-enter{
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
	opacity: 1;
	-webkit-animation: enter-animation 1.5s ease forwards;
	        animation: enter-animation 1.5s ease forwards;
}
@-webkit-keyframes enter-animation {
	0%{
		opacity: 0;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	100% {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@keyframes enter-animation {
	0%{
		opacity: 0;
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	100% {
		opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}


@media screen and (min-width: 1981px) {
	.App {
		background-color: gray;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: center;
		    justify-content: center;
	}
	.App > div:first-child {
		width: 95%;
		height: 95%;
		overflow: hidden;
	}
	.Home__blind-left, .Home__blind-right {
		display: none;
	}
	
}

@media screen and (max-width: 768px) {

.MainWrapper.on-enter{
	-webkit-transform: none;
	    -ms-transform: none;
	        transform: none;
	-webkit-animation: none;
	        animation: none;
}
}






