
.ModalMessage {
	position: fixed;
	bottom: 50vh;
	left: calc(50vw - 20vw);
	width: 40vw;
	height: 25vh;
	z-index: 100;
	background-color: #F5D5F5;
	border-radius: 10px;
	-webkit-box-shadow: 0 10px 10px -5px rgba(0,0,0,.5);
	        box-shadow: 0 10px 10px -5px rgba(0,0,0,.5);
	color: black;
	font-size: 1rem;
	font-family: 'Merriweather Sans', sans-serif;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	opacity: 0;
	-webkit-transform: translateY(-20px);
	    -ms-transform: translateY(-20px);
	        transform: translateY(-20px);
	-webkit-animation: show-modal 1s ease forwards;
	        animation: show-modal 1s ease forwards;
}

@-webkit-keyframes show-modal{
	0%{
		opacity: 0;
		-webkit-transform: translateY(-20px);
		        transform: translateY(-20px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes show-modal{
	0%{
		opacity: 0;
		-webkit-transform: translateY(-20px);
		        transform: translateY(-20px);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}
.ModalMessage button {
	padding: 5px 40px;
	border: none;
	border-radius: 8px;
	background-color: #95C1C5;
	cursor: pointer;
	-webkit-box-shadow: 0 5px 5px -3px rgba(0,0,0,.4);
	        box-shadow: 0 5px 5px -3px rgba(0,0,0,.4);
}
.ModalMessage button:hover,
.ModalMessage button:active {
	-webkit-box-shadow: none;
	        box-shadow: none;
}

@media screen and (max-width: 480px) {
	.ModalMessage {
		width: 90%;
		left: 5%;
		padding: 10px;
	}
}


